.top-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 48px;
  background-color: #2a2a2a;
  border-bottom: 1px solid #3a3a3a;
  padding: 0 20px;
  position: relative;
  z-index: 100;
}

.top-bar-left,
.top-bar-right {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 12px;
}

.top-bar-right {
  justify-content: flex-end;
}

.top-bar-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.connection-status {
  display: flex;
  align-items: center;
  gap: 8px;
  background-color: #1a1a1a;
  padding: 6px 16px;
  border-radius: 20px;
  border: 1px solid #3a3a3a;
  font-size: 13px;
  color: #e0e0e0;
  transition: all 0.2s;
  user-select: none;
}

.connection-status:hover {
  background-color: #222;
  border-color: #444;
}

.status-indicator {
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

.connection-status.connected .status-indicator {
  background-color: #4caf50;
  animation: pulse 2s infinite;
}

.connection-status.disconnected .status-indicator {
  background-color: #f44336;
  animation: none;
}

.connection-status.connecting .status-indicator {
  background-color: #ff9800;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(76, 175, 80, 0.7);
  }
  70% {
    box-shadow: 0 0 0 6px rgba(76, 175, 80, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(76, 175, 80, 0);
  }
}

.trace-button {
  background-color: #0066cc;
  color: white;
  border: none;
  padding: 8px 24px;
  border-radius: 4px;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.trace-button:hover {
  background-color: #0077dd;
  box-shadow: 0 2px 8px rgba(0, 102, 204, 0.3);
}

.trace-button:active {
  background-color: #0055aa;
  transform: translateY(1px);
}

.app-title {
  font-size: 20px;
  font-weight: 600;
  color: #e0e0e0;
  margin: 0;
  letter-spacing: -0.5px;
}

.icon-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background-color: #333333;
  color: #e0e0e0;
  border: 1px solid #4a4a4a;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
}

.icon-button:hover {
  background-color: #404040;
  border-color: #5a5a5a;
}

.icon-button:active {
  background-color: #2a2a2a;
  transform: translateY(1px);
}

/* Light theme adjustments */
.light-theme .top-bar {
  background-color: #f5f5f5;
  border-bottom-color: #e0e0e0;
}

.light-theme .app-title {
  color: #333333;
}

.light-theme .icon-button {
  background-color: #ffffff;
  color: #333333;
  border-color: #d0d0d0;
}

.light-theme .icon-button:hover {
  background-color: #f0f0f0;
  border-color: #c0c0c0;
}

.light-theme .connection-status {
  background-color: #ffffff;
  border-color: #d0d0d0;
  color: #333333;
}

.light-theme .trace-button {
  background-color: #0066cc;
  color: white;
}

/* Mobile portrait layout for TopBar */
@media (max-width: 768px) and (orientation: portrait) {
  .top-bar {
    flex-wrap: wrap;
    height: auto;
    padding: 8px 12px;
  }
  
  /* First row: Title, icons, and trace button */
  .top-bar-left,
  .top-bar-right {
    flex: none;
    gap: 8px;
  }
  
  .top-bar-left {
    order: 1;
  }
  
  .top-bar-right {
    order: 2;
    margin-left: auto;
  }
  
  /* Second row: Connection status takes full width */
  .top-bar-center {
    order: 3;
    width: 100%;
    margin-top: 8px;
  }
  
  .connection-status {
    width: 100%;
    justify-content: center;
    padding: 8px 16px;
    font-size: 12px;
  }
  
  /* Smaller app title */
  .app-title {
    font-size: 18px;
  }
  
  /* Smaller buttons */
  .icon-button {
    width: 32px;
    height: 32px;
  }
  
  .icon-button svg {
    width: 14px;
    height: 14px;
  }
  
  .trace-button {
    padding: 6px 16px;
    font-size: 12px;
  }
  
  /* Hide some buttons on very small screens if needed */
  @media (max-width: 380px) {
    .name-toggle {
      display: none;
    }
  }
}