.right-panel {
  min-width: 240px;
  width: 320px;
  max-width: 450px;
  border-left: 1px solid #3a3a3a;
  flex: 0 0 auto;
  transition: width 0.2s ease;
  order: 3;
  display: flex;
  flex-direction: column;
}

.panel-tabs {
  display: flex;
  background-color: #333333;
  border-bottom: 1px solid #3a3a3a;
}

.panel-tab {
  flex: 1;
  padding: 8px 12px;
  background: none;
  border: none;
  color: #808080;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: all 0.2s;
  position: relative;
}

.panel-tab:hover {
  color: #b0b0b0;
  background-color: #3a3a3a;
}

.panel-tab.active {
  color: #e0e0e0;
  background-color: #2a2a2a;
}

.panel-tab.active::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #0066cc;
}

.panel-content {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

/* Light theme */
body.light-theme .right-panel {
  border-left-color: #e0e0e0;
}

body.light-theme .panel-tabs {
  background-color: #f5f5f5;
  border-bottom-color: #e0e0e0;
}

body.light-theme .panel-tab {
  color: #999999;
}

body.light-theme .panel-tab:hover {
  color: #666666;
  background-color: #eeeeee;
}

body.light-theme .panel-tab.active {
  color: #333333;
  background-color: #ffffff;
}

/* Mobile adjustments */
@media (max-width: 768px) and (orientation: portrait) {
  .right-panel {
    width: 50%;
    min-width: 0;
    flex: 1;
    border-left: 1px solid #3a3a3a;
    height: 100%;
    order: initial;
  }
  
  .panel-tab {
    padding: 6px 10px;
    font-size: 11px;
  }
}