.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #1e1e1e;
  color: #e0e0e0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  overflow: hidden;
  /* Prevent text selection */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.app-content {
  display: flex;
  flex: 1;
  overflow: hidden;
  min-width: 0;
  width: 100%;
}

/* Bottom panels container - invisible on desktop, visible on mobile */
.bottom-panels {
  display: contents; /* Make container invisible on desktop */
}

.panel {
  background-color: #2a2a2a;
  border: 1px solid #3a3a3a;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.panel-header {
  background-color: #333333;
  padding: 8px 12px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-bottom: 1px solid #3a3a3a;
  user-select: none;
}

.hierarchy-panel {
  min-width: 180px;
  width: 250px;
  max-width: 350px;
  border-right: 1px solid #3a3a3a;
  flex: 0 0 auto;
  transition: width 0.2s ease;
  order: 1;
}

.properties-panel,
.right-panel {
  min-width: 220px;
  width: 280px;
  max-width: 400px;
  border-left: 1px solid #3a3a3a;
  flex: 0 0 auto;
  transition: width 0.2s ease;
  order: 3;
}

.viewport-container {
  flex: 1;
  position: relative;
  background-color: #1a1a1a;
  min-width: 0;
  overflow: hidden;
  order: 2;
  display: flex;
  flex-direction: column;
}

.tabs-container {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden;
}

.tabs-header {
  display: flex;
  background-color: #333333;
  border-bottom: 1px solid #3a3a3a;
}

.tab-button {
  padding: 8px 16px;
  background: none;
  border: none;
  color: #808080;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: all 0.2s;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
}

.tab-button:hover {
  color: #b0b0b0;
  background-color: #3a3a3a;
}

.tab-button.active {
  color: #e0e0e0;
  background-color: #2a2a2a;
}

.tab-button.active::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #0066cc;
}

.tab-icon {
  font-size: 16px;
}

.tab-content {
  flex: 1;
  overflow: hidden;
  position: relative;
}

.viewport-toolbar {
  position: absolute;
  top: 10px;
  left: 10px;
  display: flex;
  gap: 5px;
  z-index: 10;
}

.toolbar-button {
  background-color: #333333;
  border: 1px solid #4a4a4a;
  color: #e0e0e0;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s;
}

.toolbar-button:hover {
  background-color: #404040;
  border-color: #5a5a5a;
}

.toolbar-button.active {
  background-color: #0066cc;
  border-color: #0077dd;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Global scrollbar theming for dark theme */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #1a1a1a;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #4a4a4a;
  border-radius: 4px;
  transition: background 0.2s;
}

::-webkit-scrollbar-thumb:hover {
  background: #5a5a5a;
}

::-webkit-scrollbar-corner {
  background: #1a1a1a;
}

/* Light theme styles */
body.light-theme {
  background-color: #f5f5f5;
  color: #333333;
}

body.light-theme .app-container {
  background-color: #f5f5f5;
  color: #333333;
}

body.light-theme .panel {
  background-color: #ffffff;
  border-color: #e0e0e0;
}

body.light-theme .panel-header {
  background-color: #f5f5f5;
  border-bottom-color: #e0e0e0;
}

body.light-theme .viewport-container {
  background-color: #d8d8d8;
}

body.light-theme .tabs-header {
  background-color: #f5f5f5;
  border-bottom-color: #e0e0e0;
}

body.light-theme .tab-button {
  color: #999999;
}

body.light-theme .tab-button:hover {
  color: #666666;
  background-color: #eeeeee;
}

body.light-theme .tab-button.active {
  color: #333333;
  background-color: #ffffff;
}

body.light-theme .toolbar-button {
  background-color: #ffffff;
  border-color: #d0d0d0;
  color: #333333;
}

body.light-theme .toolbar-button:hover {
  background-color: #f0f0f0;
  border-color: #c0c0c0;
}

body.light-theme .toolbar-button.active {
  background-color: #0066cc;
  border-color: #0077dd;
  color: white;
}

/* Light theme scrollbar */
body.light-theme ::-webkit-scrollbar-track {
  background: #f0f0f0;
}

body.light-theme ::-webkit-scrollbar-thumb {
  background: #c0c0c0;
}

body.light-theme ::-webkit-scrollbar-thumb:hover {
  background: #a0a0a0;
}

body.light-theme ::-webkit-scrollbar-corner {
  background: #f0f0f0;
}

/* Responsive adjustments */
@media (max-width: 1400px) {
  .hierarchy-panel {
    width: 18%;
    min-width: 160px;
  }
  
  .properties-panel {
    width: 20%;
    min-width: 200px;
  }
}

@media (max-width: 1200px) {
  .hierarchy-panel {
    width: 200px;
    min-width: 160px;
  }
  
  .properties-panel {
    width: 240px;
    min-width: 200px;
  }
}

@media (max-width: 1000px) {
  .hierarchy-panel {
    width: 180px;
    min-width: 150px;
  }
  
  .properties-panel {
    width: 220px;
    min-width: 190px;
  }
  
  .property-group > .property-label {
    min-width: 40px;
  }
}

@media (max-width: 850px) {
  .app-content {
    overflow-x: auto;
    overflow-y: hidden;
  }
  
  .hierarchy-panel {
    width: 170px;
    flex: 0 0 170px;
  }
  
  .properties-panel {
    width: 210px;
    flex: 0 0 210px;
  }
  
  .viewport-container {
    min-width: 300px;
  }
  
  /* Scrollbar styling */
  .app-content::-webkit-scrollbar {
    height: 8px;
  }
  
  .app-content::-webkit-scrollbar-track {
    background: #1a1a1a;
  }
  
  .app-content::-webkit-scrollbar-thumb {
    background: #4a4a4a;
    border-radius: 4px;
  }
  
  body.light-theme .app-content::-webkit-scrollbar-track {
    background: #f5f5f5;
  }
  
  body.light-theme .app-content::-webkit-scrollbar-thumb {
    background: #c0c0c0;
  }
}

/* Mobile portrait layout */
@media (max-width: 768px) and (orientation: portrait) {
  .app-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
  }
  
  .app-content {
    flex-direction: column;
    overflow: hidden;
    flex: 1;
    height: calc(100vh - 64px); /* Adjust for taller mobile TopBar */
  }
  
  /* Viewport takes upper half */
  .viewport-container {
    flex: 1;
    width: 100%;
    min-width: 0;
    order: 1;
    height: 50%;
  }
  
  /* Bottom panel container for hierarchy and properties */
  .bottom-panels {
    display: flex;
    flex: 0 0 50vh;
    width: 100%;
    order: 2;
    overflow: hidden;
  }
  
  .hierarchy-panel {
    width: 50%;
    min-width: 0;
    flex: 1;
    border-right: 1px solid #3a3a3a;
    border-left: none;
    height: 100%;
    order: initial; /* Reset order for mobile */
  }
  
  .properties-panel,
  .right-panel {
    width: 50%;
    min-width: 0;
    flex: 1;
    border-left: 1px solid #3a3a3a;
    height: 100%;
    order: initial; /* Reset order for mobile */
  }
  
  /* Adjust panel headers for mobile */
  .panel-header {
    padding: 6px 10px;
    font-size: 11px;
  }
  
  /* Reduce padding in panels for mobile */
  .properties-content,
  .hierarchy-content {
    padding: 8px;
  }
  
  /* Make property labels even more compact */
  .property-group > .property-label {
    min-width: 35px !important;
    font-size: 11px;
  }
  
  .property-section {
    margin-bottom: 8px;
    padding-bottom: 6px;
  }
  
  .section-header {
    font-size: 12px;
    margin-bottom: 6px;
  }
  
  /* Smaller inputs for mobile */
  .vector-input input,
  .number-input {
    font-size: 11px;
    padding: 3px 6px;
  }
  
  .draggable-number-input .input-label {
    font-size: 10px;
  }
  
  /* Hide scrollbars on app-content since we're using column layout */
  .app-content::-webkit-scrollbar {
    display: none;
  }
  
  /* Adjust hierarchy visibility column for mobile */
  .hierarchy-content {
    background: linear-gradient(to right, #1a1a1a 28px, transparent 28px);
    padding: 0;
  }
  
  .hierarchy-row .visibility-toggle {
    width: 28px;
    font-size: 11px;
  }
  
  .hierarchy-row .visibility-toggle::after {
    display: none;
  }
  
  .hierarchy-item {
    padding: 2px 6px;
    font-size: 11px;
  }
  
  .hierarchy-row {
    height: 22px;
  }
  
  .expand-toggle {
    width: 12px;
    height: 12px;
    font-size: 7px;
  }
  
  .expand-spacer {
    width: 14px;
  }
  
  .hierarchy-icon {
    font-size: 11px;
    margin-right: 4px;
  }
  
  /* Light theme adjustments for mobile */
  .light-theme .hierarchy-content {
    background: linear-gradient(to right, #f0f0f0 28px, transparent 28px);
  }
}