.drag-drop-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  pointer-events: none;
}

.drag-drop-content {
  text-align: center;
  color: white;
}

.drag-drop-icon {
  font-size: 64px;
  margin-bottom: 20px;
}

.drag-drop-content h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.drag-drop-content p {
  font-size: 16px;
  opacity: 0.8;
}

.upload-progress-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.upload-progress-content {
  background-color: #2a2a2a;
  padding: 30px;
  border-radius: 8px;
  min-width: 300px;
  text-align: center;
}

.upload-progress-content h3 {
  margin-bottom: 20px;
  color: #e0e0e0;
}

.progress-bar {
  width: 100%;
  height: 20px;
  background-color: #1a1a1a;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 10px;
}

.progress-fill {
  height: 100%;
  background-color: #0066cc;
  transition: width 0.3s ease;
}

.upload-progress-content p {
  color: #e0e0e0;
  margin: 0;
}