.timeline-panel {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #2a2a2a;
  border-top: 1px solid #3a3a3a;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 10;
}

.timeline-panel.collapsed {
  height: 30px;
}

.timeline-panel.expanded {
  animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}

.timeline-header {
  display: flex;
  align-items: center;
  padding: 6px 12px;
  background-color: #333333;
  border-bottom: 1px solid #3a3a3a;
  cursor: default;
  user-select: none;
  position: relative;
}

.timeline-header.expanded {
  cursor: ns-resize;
}

.timeline-toggle-icon {
  margin-right: 8px;
  font-size: 10px;
  color: #808080;
  transition: transform 0.2s;
  cursor: pointer;
  padding: 4px 8px;
  margin: -4px 0 -4px -8px;
  user-select: none;
}

.timeline-toggle-icon:hover {
  color: #a0a0a0;
}

.timeline-title {
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: #e0e0e0;
  cursor: pointer;
  user-select: none;
  padding: 4px 8px;
  margin: -4px -8px;
}

.timeline-title:hover {
  color: #ffffff;
}

.timeline-content {
  display: flex;
  flex-direction: column;
  height: calc(100% - 30px);
  position: relative;
}

.timeline-toolbar {
  display: flex;
  align-items: center;
  padding: 8px;
  gap: 8px;
  background-color: #252525;
  border-bottom: 1px solid #3a3a3a;
}

.timeline-button {
  background-color: #333333;
  border: 1px solid #4a4a4a;
  color: #e0e0e0;
  padding: 4px 8px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.timeline-button:hover {
  background-color: #404040;
  border-color: #5a5a5a;
}

.timeline-icon {
  font-size: 10px;
}

.timeline-frame-info {
  display: flex;
  align-items: center;
  margin-left: auto;
  gap: 4px;
  font-size: 12px;
  color: #b0b0b0;
}

.frame-label {
  margin-right: 4px;
}

.frame-input {
  width: 50px;
  background-color: #1a1a1a;
  border: 1px solid #3a3a3a;
  color: #e0e0e0;
  padding: 2px 4px;
  border-radius: 3px;
  font-size: 12px;
  text-align: center;
}

.frame-separator {
  margin: 0 2px;
}

.timeline-tracks-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}

.timeline-ruler-container {
  display: flex;
  height: 30px;
  background-color: #222222;
  border-bottom: 1px solid #3a3a3a;
  flex-shrink: 0;
  position: relative;
  overflow: hidden;
}

.ruler-header {
  width: 120px;
  min-width: 120px;
  background-color: #252525;
  border-right: 1px solid #3a3a3a;
  flex-shrink: 0;
  position: sticky;
  left: 0;
  z-index: 16; /* Higher than track headers */
}

.timeline-tracks {
  flex: 1;
  overflow-y: auto;
  overflow-x: auto;
  position: relative;
  display: flex;
  flex-direction: column;
}

.timeline-track {
  display: flex;
  border-bottom: 1px solid #3a3a3a;
  height: 30px;
}

.track-header {
  width: 120px;
  min-width: 120px;
  padding: 0 12px;
  background-color: #252525;
  border-right: 1px solid #3a3a3a;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  position: sticky;
  left: 0;
  z-index: 15; /* Higher than scrubber */
}

.track-name {
  font-size: 11px;
  color: #b0b0b0;
}

.track-content {
  flex: 1;
  position: relative;
  background-color: #1a1a1a;
  min-width: 2410px; /* Match ruler width */
}

.timeline-ruler {
  flex: 1;
  overflow-x: auto;
  overflow-y: hidden;
  scrollbar-width: none;
}

.timeline-ruler::-webkit-scrollbar {
  display: none;
}

.ruler-content {
  display: flex;
  min-width: 2410px; /* 241 ticks * 10px */
}

.ruler-mark {
  width: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-shrink: 0;
}

.ruler-tick {
  width: 1px;
  background-color: #3a3a3a;
  margin-bottom: 2px;
}

.ruler-tick.major {
  height: 10px;
  background-color: #5a5a5a;
}

.ruler-tick.minor {
  height: 5px;
  background-color: #3a3a3a;
}

.ruler-number {
  font-size: 10px;
  color: #808080;
  padding-left: 2px;
  user-select: none;
}

.track-keys {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.keyframe {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: 8px;
  height: 8px;
  background-color: #0066cc;
  border: 1px solid #0088ff;
  cursor: grab;
  transition: all 0.2s;
  z-index: 1;
}

.keyframe:hover {
  transform: translate(-50%, -50%) rotate(45deg) scale(1.2);
  background-color: #0088ff;
}

.keyframe.dragging {
  cursor: grabbing;
  background-color: #ff4444;
  border-color: #ff6666;
  transform: translate(-50%, -50%) rotate(45deg) scale(1.3);
  z-index: 10;
}

.timeline-scrubber {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 21px; /* Increased width for better mouse picking */
  cursor: ew-resize;
  transition: left 0.1s ease-out;
  z-index: 3; /* Lower than track headers */
  transform: translateX(-10px); /* Center the click area */
}

.timeline-scrubber:hover .scrubber-head {
  background-color: #ff6666;
  transform: translateX(-50%) rotate(45deg) scale(1.1);
}

.timeline-scrubber.dragging {
  transition: none;
}

.timeline-scrubber.dragging .scrubber-head {
  background-color: #ff6666;
  transform: translateX(-50%) rotate(45deg) scale(1.2);
}

.scrubber-head {
  position: absolute;
  top: -6px;
  left: 50%;
  transform: translateX(-50%) rotate(45deg); /* Center and point downward */
  width: 13px;
  height: 13px;
  background-color: #ff4444;
  border-radius: 50% 50% 0 50%;
  cursor: ew-resize;
  transition: all 0.2s;
  pointer-events: none;
}

/* Visual hover effect for the wider click area */
.timeline-scrubber:hover .scrubber-line {
  width: 2px;
  background-color: #ff6666;
}

.scrubber-line {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 1px;
  background-color: #ff4444;
  pointer-events: none;
}

/* Light theme */
body.light-theme .timeline-panel {
  background-color: #ffffff;
  border-color: #e0e0e0;
}

body.light-theme .timeline-header {
  background-color: #f5f5f5;
  border-bottom-color: #e0e0e0;
}

body.light-theme .timeline-ruler-container {
  background-color: #f0f0f0;
  border-bottom-color: #e0e0e0;
}

body.light-theme .ruler-header {
  background-color: #fafafa;
  border-right-color: #e0e0e0;
}

body.light-theme .ruler-tick.major {
  background-color: #a0a0a0;
}

body.light-theme .ruler-tick.minor {
  background-color: #d0d0d0;
}

body.light-theme .ruler-number {
  color: #666666;
}

body.light-theme .timeline-toggle-icon {
  color: #999999;
}

body.light-theme .timeline-title {
  color: #333333;
}

body.light-theme .timeline-toolbar {
  background-color: #fafafa;
  border-bottom-color: #e0e0e0;
}

body.light-theme .timeline-button {
  background-color: #ffffff;
  border-color: #d0d0d0;
  color: #333333;
}

body.light-theme .timeline-button:hover {
  background-color: #f0f0f0;
  border-color: #c0c0c0;
}

body.light-theme .timeline-frame-info {
  color: #666666;
}

body.light-theme .frame-input {
  background-color: #ffffff;
  border-color: #d0d0d0;
  color: #333333;
}

body.light-theme .timeline-track {
  border-bottom-color: #e0e0e0;
}

body.light-theme .track-header {
  background-color: #fafafa;
  border-right-color: #e0e0e0;
}

body.light-theme .track-name {
  color: #666666;
}

body.light-theme .track-content {
  background-color: #f5f5f5;
}

body.light-theme .timeline-ruler {
  background-color: #eeeeee;
  border-bottom-color: #e0e0e0;
}

body.light-theme .ruler-mark {
  border-right-color: #e0e0e0;
}

body.light-theme .ruler-number {
  color: #999999;
}

/* Resize handle */
.timeline-resize-handle {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: transparent;
  cursor: ns-resize;
}

.timeline-resize-handle:hover {
  background: rgba(0, 102, 204, 0.3);
}

.timeline-panel.resizing {
  user-select: none;
}

.timeline-panel.resizing .timeline-header {
  cursor: ns-resize;
}

/* Scrollbar theming */
.timeline-tracks::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.timeline-tracks::-webkit-scrollbar-track {
  background: #1a1a1a;
  border-radius: 4px;
}

.timeline-tracks::-webkit-scrollbar-thumb {
  background: #4a4a4a;
  border-radius: 4px;
  transition: background 0.2s;
}

.timeline-tracks::-webkit-scrollbar-thumb:hover {
  background: #5a5a5a;
}

.timeline-tracks::-webkit-scrollbar-corner {
  background: #1a1a1a;
}

/* Light theme scrollbars */
body.light-theme .timeline-tracks::-webkit-scrollbar-track {
  background: #f0f0f0;
}

body.light-theme .timeline-tracks::-webkit-scrollbar-thumb {
  background: #c0c0c0;
}

body.light-theme .timeline-tracks::-webkit-scrollbar-thumb:hover {
  background: #a0a0a0;
}

body.light-theme .timeline-tracks::-webkit-scrollbar-corner {
  background: #f0f0f0;
}

/* Responsive adjustments */

/* Mobile layout */
@media (max-width: 768px) and (orientation: portrait) {
  .timeline-panel {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 20;
  }
  
  .timeline-panel.collapsed {
    height: 30px;
  }
  
  .timeline-panel.expanded {
    height: 50vh !important; /* Match other panels height */
  }
  
  .timeline-header.expanded {
    cursor: default !important; /* No resize on mobile */
  }
  
  .track-header {
    width: 80px; /* Smaller track header for mobile */
    min-width: 80px;
  }
  
  .ruler-header {
    width: 80px;
    min-width: 80px;
  }
  
  .timeline-frame-info {
    font-size: 11px;
  }
  
  .frame-input {
    width: 40px;
    font-size: 11px;
  }
  
  .timeline-button {
    padding: 3px 6px;
    font-size: 11px;
  }
  
  .timeline-toolbar {
    padding: 6px;
    gap: 6px;
  }
}