.properties-content {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
}

.no-selection {
  text-align: center;
  color: #666;
  padding: 20px;
  font-style: italic;
}

.property-section {
  margin-bottom: 12px;
  border-bottom: 1px solid #3a3a3a;
  padding-bottom: 8px;
}

.property-section:last-child {
  border-bottom: none;
}

.section-header {
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 8px;
  color: #e0e0e0;
}

.property-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  font-size: 13px;
}

.property-label {
  font-size: 12px;
  color: #b0b0b0;
  margin-bottom: 4px;
  display: block;
}

.property-value {
  color: #e0e0e0;
}

.property-group {
  margin-bottom: 6px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.property-group:last-child {
  margin-bottom: 0;
}

.property-group > .property-label {
  min-width: 45px;
  margin-bottom: 0;
  font-size: 12px;
}

.vector-input {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 3px;
  flex: 1;
}

.vector-input input {
  background-color: #1a1a1a;
  border: 1px solid #3a3a3a;
  color: #e0e0e0;
  padding: 4px 8px;
  border-radius: 3px;
  font-size: 12px;
  width: 100%;
}

.vector-input input:focus {
  outline: none;
  border-color: #0066cc;
}

.color-input {
  flex: 1;
  height: 28px;
  background-color: #1a1a1a;
  border: 1px solid #3a3a3a;
  border-radius: 3px;
  cursor: pointer;
}

.material-input-group {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 8px;
}

.slider-input {
  flex: 1;
  appearance: none;
  height: 4px;
  background: #3a3a3a;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
}

.slider-input:hover {
  opacity: 1;
}

.slider-input::-webkit-slider-thumb {
  appearance: none;
  width: 12px;
  height: 12px;
  background: #0066cc;
  cursor: pointer;
  border-radius: 50%;
}

.slider-input::-moz-range-thumb {
  width: 12px;
  height: 12px;
  background: #0066cc;
  cursor: pointer;
  border-radius: 50%;
}

.slider-value {
  font-size: 11px;
  color: #b0b0b0;
  width: 32px;
  text-align: right;
}

.properties-content::-webkit-scrollbar {
  width: 8px;
}

.properties-content::-webkit-scrollbar-track {
  background: #1a1a1a;
}

.properties-content::-webkit-scrollbar-thumb {
  background: #4a4a4a;
  border-radius: 4px;
}

.properties-content::-webkit-scrollbar-thumb:hover {
  background: #5a5a5a;
}

/* Light theme adjustments */
.light-theme .no-selection {
  color: #999999;
}

.light-theme .property-section {
  border-bottom-color: #e0e0e0;
}

.light-theme .section-header {
  color: #333333;
}

.light-theme .property-label {
  color: #666666;
}

.light-theme .property-value {
  color: #333333;
}

.light-theme .vector-input input {
  background-color: #f5f5f5;
  border-color: #d0d0d0;
  color: #333333;
}

.light-theme .vector-input input:focus {
  border-color: #0066cc;
}

.light-theme .color-input {
  background-color: #f5f5f5;
  border-color: #d0d0d0;
}

.light-theme .slider-input {
  background: #d0d0d0;
}

.light-theme .slider-input::-webkit-slider-thumb {
  background: #0066cc;
}

.light-theme .slider-input::-moz-range-thumb {
  background: #0066cc;
}

.light-theme .slider-value {
  color: #666666;
}

.light-theme .properties-content::-webkit-scrollbar-track {
  background: #f5f5f5;
}

.light-theme .properties-content::-webkit-scrollbar-thumb {
  background: #c0c0c0;
}

.light-theme .properties-content::-webkit-scrollbar-thumb:hover {
  background: #b0b0b0;
}

/* Responsive adjustments */
@media (max-width: 900px) {
  .property-group > .property-label {
    min-width: 40px !important;
  }
  
  .vector-input {
    gap: 2px;
  }
}