.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 20000;
}

.modal-content {
  background-color: #2a2a2a;
  border-radius: 8px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  border: 1px solid #3a3a3a;
  width: 90%;
  max-width: 500px;
  animation: modalSlideIn 0.2s ease-out;
}

.server-selector-modal {
  max-width: 600px;
}

@keyframes modalSlideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid #3a3a3a;
}

.modal-header h2 {
  margin: 0;
  font-size: 20px;
  color: #e0e0e0;
}

.modal-close {
  background: none;
  border: none;
  color: #666;
  font-size: 28px;
  cursor: pointer;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: all 0.2s;
}

.modal-close:hover {
  color: #e0e0e0;
  background-color: #3a3a3a;
}

.modal-body {
  padding: 24px;
}

.modal-body label {
  display: block;
  margin-bottom: 8px;
  font-size: 14px;
  color: #b0b0b0;
}


.server-help-text {
  margin: 8px 0 0 0;
  font-size: 12px;
  color: #888;
}

.server-input-group {
  margin-bottom: 24px;
}

.input-row {
  display: flex;
  gap: 12px;
  margin-bottom: 8px;
}

.input-field {
  display: flex;
  flex-direction: column;
}

.address-field {
  flex: 1;
}

.port-field {
  width: 100px;
}

.input-field label {
  display: block;
  margin-bottom: 6px;
  font-size: 13px;
  color: #b0b0b0;
}

.input-field input {
  width: 100%;
  padding: 8px 10px;
  background-color: #1a1a1a;
  border: 1px solid #3a3a3a;
  border-radius: 4px;
  color: #e0e0e0;
  font-size: 14px;
  font-family: 'Consolas', 'Monaco', monospace;
}

.server-history {
  margin-top: 24px;
  border-top: 1px solid #3a3a3a;
  padding-top: 16px;
}

.server-history h3 {
  margin: 0 0 12px 0;
  font-size: 14px;
  color: #b0b0b0;
  font-weight: 500;
}

.history-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  max-height: 200px;
  overflow-y: auto;
}

.history-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 12px;
  background-color: #1a1a1a;
  border: 1px solid #3a3a3a;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s;
}

.history-item:hover {
  background-color: #242424;
  border-color: #4a4a4a;
}

.history-item.online {
  border-color: #2a4a2a;
  animation: breathe 2s ease-in-out infinite;
}

@keyframes breathe {
  0% {
    box-shadow: 0 0 0 0 rgba(76, 175, 80, 0.4);
  }
  50% {
    box-shadow: 0 0 0 4px rgba(76, 175, 80, 0.2);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(76, 175, 80, 0.4);
  }
}

.history-server {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.history-address {
  font-size: 13px;
  color: #e0e0e0;
  font-family: 'Consolas', 'Monaco', monospace;
}

.history-time {
  font-size: 11px;
  color: #888;
}

.history-item .status-indicator {
  width: 12px;
  height: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  position: relative;
}

.history-item .status-indicator::before {
  content: '';
  width: 4px;
  height: 4px;
  border-radius: 50%;
  display: block;
  position: absolute;
}

.history-item .status-indicator.online::before {
  background-color: #4caf50;
}

.history-item .status-indicator.offline::before {
  background-color: #f44336;
}

.history-item .status-indicator.checking::before {
  background-color: #ff9800;
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.7;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.history-list::-webkit-scrollbar {
  width: 6px;
}

.history-list::-webkit-scrollbar-track {
  background: #1a1a1a;
  border-radius: 3px;
}

.history-list::-webkit-scrollbar-thumb {
  background: #4a4a4a;
  border-radius: 3px;
}

.history-list::-webkit-scrollbar-thumb:hover {
  background: #5a5a5a;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  padding: 16px 24px;
  border-top: 1px solid #3a3a3a;
}

.modal-footer:has(> :only-child) {
  justify-content: center;
}

.cancel-button {
  background-color: #333;
  color: #e0e0e0;
  border: 1px solid #444;
  padding: 8px 20px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;
}

.cancel-button:hover {
  background-color: #404040;
  border-color: #555;
}

.cancel-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.connect-modal-button {
  background-color: #0066cc;
  color: white;
  border: none;
  padding: 8px 20px;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.connect-modal-button:hover {
  background-color: #0077dd;
  box-shadow: 0 2px 8px rgba(0, 102, 204, 0.3);
}

.connect-modal-button:active {
  background-color: #0055aa;
  transform: translateY(1px);
}

.connect-modal-button:disabled {
  background-color: #444;
  cursor: not-allowed;
  box-shadow: none;
}

.continue-offline-button {
  background-color: #666666;
  color: #e0e0e0;
  border: 1px solid #777777;
  padding: 8px 20px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;
}

.continue-offline-button:hover {
  background-color: #777777;
  border-color: #888888;
}

.continue-offline-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Light theme adjustments */
.light-theme .modal-overlay {
  background-color: rgba(255, 255, 255, 0.8);
}

.light-theme .modal-content {
  background-color: #ffffff;
  border-color: #e0e0e0;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
}

.light-theme .modal-header {
  border-bottom-color: #e0e0e0;
}

.light-theme .modal-header h2 {
  color: #333333;
}

.light-theme .modal-close {
  color: #999999;
}

.light-theme .modal-close:hover {
  color: #333333;
  background-color: #f0f0f0;
}

.light-theme .modal-body label {
  color: #666666;
}

.light-theme .modal-body input {
  background-color: #f5f5f5;
  border-color: #d0d0d0;
  color: #333333;
}

.light-theme .modal-body input:focus {
  border-color: #0066cc;
}

.light-theme .server-help-text {
  color: #666;
}

.light-theme .preset-button {
  background-color: #f0f0f0;
  border-color: #d0d0d0;
  color: #666666;
}

.light-theme .preset-button:hover {
  background-color: #e0e0e0;
  color: #333333;
  border-color: #c0c0c0;
}

.light-theme .modal-footer {
  border-top-color: #e0e0e0;
}

.light-theme .cancel-button {
  background-color: #f0f0f0;
  color: #333333;
  border-color: #d0d0d0;
}

.light-theme .cancel-button:hover {
  background-color: #e0e0e0;
  border-color: #c0c0c0;
}

.light-theme .connect-modal-button {
  background-color: #0066cc;
  color: white;
}

.light-theme .connect-modal-button:hover {
  background-color: #0077dd;
}

.light-theme .connect-modal-button:disabled {
  background-color: #cccccc;
  color: #999999;
}

/* Light theme for new elements */
.light-theme .input-field label {
  color: #666666;
}

.light-theme .input-field input {
  background-color: #f5f5f5;
  border-color: #d0d0d0;
