/* Tutorial section */
.tutorial-section {
  margin-top: 20px;
  padding: 16px;
  background-color: #1a1a1a;
  border: 1px solid #3a3a3a;
  border-radius: 6px;
}

.tutorial-text {
  margin: 0;
  font-size: 13px;
  color: #b0b0b0;
  line-height: 1.4;
}

.tutorial-link {
  color: #0066cc;
  text-decoration: none;
  font-weight: 500;
}

.tutorial-link:hover {
  color: #0077dd;
  text-decoration: underline;
}

/* Light theme for tutorial section */
.light-theme .tutorial-section {
  background-color: #f5f5f5;
  border-color: #e0e0e0;
}

.light-theme .tutorial-text {
  color: #666666;
}

.light-theme .tutorial-link {
  color: #0066cc;
}

.light-theme .tutorial-link:hover {
  color: #0077dd;
}