.viewport-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.viewport-container canvas {
  outline: none;
}

.viewport-toolbar {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 5px;
  background: rgba(40, 40, 40, 0.9);
  padding: 5px;
  border-radius: 4px;
  z-index: 100;
}

.toolbar-button {
  padding: 6px 12px;
  background: rgba(60, 60, 60, 0.8);
  color: white;
  border: 1px solid rgba(80, 80, 80, 0.5);
  border-radius: 3px;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s;
}

.toolbar-button:hover {
  background: rgba(80, 80, 80, 0.8);
  border-color: rgba(100, 100, 100, 0.8);
}

.toolbar-button.active {
  background: rgba(0, 120, 255, 0.8);
  border-color: rgba(0, 140, 255, 0.9);
}

.toolbar-separator {
  width: 1px;
  background: rgba(100, 100, 100, 0.5);
  margin: 0 5px;
}

/* Light theme styles */
body.light-theme .viewport-toolbar {
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid rgba(220, 220, 220, 0.9);
}

body.light-theme .toolbar-button {
  background: rgba(245, 245, 245, 0.9);
  color: #333333;
  border: 1px solid rgba(200, 200, 200, 0.8);
}

body.light-theme .toolbar-button:hover {
  background: rgba(230, 230, 230, 0.9);
  border-color: rgba(180, 180, 180, 0.9);
}

body.light-theme .toolbar-button.active {
  background: rgba(0, 120, 255, 0.9);
  color: white;
  border-color: rgba(0, 100, 220, 0.9);
}

body.light-theme .toolbar-separator {
  background: rgba(200, 200, 200, 0.5);
}