.draggable-number-input {
  display: flex;
  align-items: center;
  gap: 2px;
}

.input-label {
  min-width: 10px;
  font-size: 12px;
  font-weight: 500;
  color: var(--text-secondary);
  user-select: none;
}

.input-container {
  position: relative;
  flex: 1;
  min-width: 60px;
  display: flex;
  align-items: center;
  background: var(--input-bg);
  border: 1px solid var(--input-border);
  border-radius: 4px;
  overflow: hidden;
  transition: border-color 0.2s ease;
}

.input-container:hover {
  border-color: var(--primary-color);
}

.input-container.dragging {
  border-color: var(--primary-color);
  background: var(--input-bg-hover);
}

.input-container.editing {
  border-color: var(--accent-color);
}

.number-input {
  flex: 1;
  padding: 4px 8px;
  background: transparent;
  border: none;
  font-size: 12px;
  font-family: 'Consolas', 'Monaco', monospace;
  color: var(--text-primary);
  outline: none;
  min-width: 0;
}

.number-input:disabled {
  color: var(--text-primary);
  cursor: ew-resize;
}

.input-suffix {
  padding-right: 8px;
  padding-left: 4px;
  font-size: 11px;
  color: var(--text-secondary);
  user-select: none;
}

.drag-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: ew-resize;
}

/* Dark theme (default) */
:root {
  --bg-tertiary: #2a2a2a;
  --border-color: #444;
  --primary-color: #64b5f6;
  --accent-color: #81c784;
  --text-primary: #f0f0f0;
  --text-secondary: #999;
  --input-bg: #1a1a1a;
  --input-border: #3a3a3a;
  --input-bg-hover: #1e1e1e;
}

/* Light theme adjustments */
.light-theme .draggable-number-input {
  --bg-tertiary: #f5f5f5;
  --border-color: #ddd;
  --primary-color: #2196f3;
  --accent-color: #4caf50;
  --text-primary: #333;
  --text-secondary: #666;
  --input-bg: #f5f5f5;
  --input-border: #d0d0d0;
  --input-bg-hover: #efefef;
}

/* Visual feedback during drag */
.draggable-number-input .input-container.dragging::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 3px;
  background: var(--primary-color);
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
}